@import url(https://fonts.googleapis.com/css2?family=Nunito&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Nunito:wght@400;500;700;800&display=swap);
* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: 'Nunito', sans-serif;
}

.home_header {
  text-align: center;
  font-size: 36pt;
  padding: 100px;
}

.new_text {
  height: 100vh;
}

.slider {
  position: relative;
  /* height: 40vh; */
  display: flex;
  justify-content: center;
  align-items: center;
}

.slide_image {
  width: 40vw;
  height: 100%;
  border-radius: 10px;
}

.right-arrow {
  position: absolute;
  top: 50%;
  right: 15%;
  font-size: 3rem;
  color: #000;
  z-index: 1;
  cursor: pointer;
  -webkit-user-select: none;
      -ms-user-select: none;
          user-select: none;
}

.left-arrow {
  position: absolute;
  top: 50%;
  left: 15%;
  font-size: 3rem;
  color: #000;
  z-index: 1;
  cursor: pointer;
  -webkit-user-select: none;
      -ms-user-select: none;
          user-select: none;
}

.slide {
  /* opacity: 0.8; */
  transition-duration: 1s ease;
}

.slide.active {
  /* opacity: 1; */
  transition-duration: 1s;
  -webkit-transform: scale(1.01);
          transform: scale(1.01);
}

@media screen and (max-width: 480px) {

  .home_header {
    font-size: 24pt;
    padding: 20px;
  }

  .new_text {
    height: 40vh;
  }

  .right-arrow {
    right: 5px;
    top: 40%;
  }

  .left-arrow {
    left: 5px;
    top: 40%;
  }

  .slide_image {
    width: 70vw;
  }
}


/* CONTACT INFO TO THE LEFT */

.contactInfo {
  float: left;
  width: 40%;
  margin-top: 100px;
  padding: 150px 100px 100px 100px;
  display: flex;
  flex-direction: column;
}

.contactInfo h3 {
  margin-bottom: 100px;
}

.contactInfo p {
  font-size: 14pt;
  margin: 10px;
}

.iconInline {
  display: flex;
}

.contactIcon {
  font-size: 16pt;
  margin: 12px 10px;
  color: black;
}

/* CONTACT FORM */

.formCont {
  width: 60%;
  float: right;
  background-color: #eaf4f8;
  display: flex;
  flex-direction: column;
  padding: 100px;
  margin-top: 100px
}

.formHeader {
  color: black;
  margin: 0;
  text-align: center;
}

.formSubHdr {
  color: black;
  font-size: 20pt;
  font-weight: lighter;
}

form {
  width: 100%;
  height: 450px;
  background-color: #eaf4f8;
}

.inputCont {
  margin: 20px;
}

.inputCont label {
  color: grey;
}

.inputCont input {
  height: 30px;
  width: 100%;
  border: none;
  border-radius: 5px;
  padding: 20px;
  font-size: 12pt;
}

.inputCont textarea {
  width: 100%;
  height: 200px;
  border: none;
  border-radius: 5px;
  margin-top: 20px;
  padding: 20px;
  font-size: 12pt;
}

.submitBtn {
  background-color: white;
  padding: 7px 30px;
  font-size: 15pt;
  border: none;
  border-radius: 5px;
  margin-left: 45%;
}

.submitBtn:hover {
  background-color: #88b3da;
  color: white;
  cursor: pointer;
}

@media screen and (max-width: 480px) {

  .contactInfo {
    width: 100%;
    margin-top: 0;
    padding: 20px;
  }

  .contactInfo h3 {
    display: none;
  }

  .formCont {
    width: 100%;
    margin: 0;
    padding: 10px;
  }

  .formHeader {
    font-size: 24pt;
  }

  .formSubHdr {
    font-size: 14pt;
  }

  .submitBtn {
    margin-left: 30%;
    background-color: #88b3da;
    color: white;
  }

}

