/* Container needed to position the button. Adjust the width as needed */
.container {
  position: relative;
  align-items: center;
}

/* Make the image responsive */
.container img {
  align-items: center;
  width: 79vw;
  height: auto;
}

/* Style the button and place it in the middle of the container/image */
.container .btn {
  position: absolute;
  top: 35%;
  left: 0%;
  background-color: white;
  color: #26428b;
  font-size: 7vh;
  font-weight: bold;
  padding: 12px 24px;
  border: none;
  cursor: pointer;
  border-radius: 5px;
  align-items: center;
}

.container .btn:hover {
  background-color: #e6f2f6;
}

@media screen and (max-width: 560px) {
  .column {
    display: block;
    width: 100%;
    height: 40vmax;
    padding: .5EM;
  }
  .hh2.col {
    font-size: 6vmax;
    margin-left: 2vw;
    margin-right: 2vw;
    text-align: center;
  }
  .hh3 {
    font-size: 6vmin;
    margin-bottom: 10%;
    margin-top: 1.5vh;
  }
  .container img {
    width: 79vw;
    height: auto;
  }
  .container .btn {
    position: absolute;
    top: 9%;
    left: 8%;
    background-color: white;
    color: #26428b;
    font-size: 6vmin;
    font-weight: bold;
    padding: 5px 15px;
    border: none;
    cursor: pointer;
    border-radius: 5px;
  }
  img.left {
    float: right;
  }
}

