@import url('https://fonts.googleapis.com/css2?family=Nunito:wght@400;500;700;800&display=swap');

html * {
  font-family: Nunito, sans-serif;
}

centerAllCont {  /*do i wanna resize fonts with em*/
  display: flex;
  width: 100%;
  flex-direction: column;
  align-items: center;
}

.hh1 {
  text-align: center;
  font-size: 7vh;
  border-bottom: 0px;
  margin: 25px 0 25px 0;
  padding: 20px;
  width: 80%;
}

.hh1.becoming {
  margin: 25px 0 0 0;
  padding: 20px 20px 10px 20px;
}

.hh2 {
  text-align: center;
  color:#26428b;
}

.hh2.col {
  font-size: 7vh;
  margin-left: 2vw;
  margin-right: 2vw;
  text-align: center;
}

.hh2.col.white {
  color:white;
}

.hh2.rect {
  font-size: 5.5vh;
  padding-top: 2vh;
  padding-bottom: 1vh;
  text-align: left;
  padding-left: 2vw;
}

.hh3 {
  font-size: 3.5vh;
  color:#26428b;
  font-weight: normal;
}

.hh3.col {
  text-align: center;
  margin-left: 2vw;
  margin-right: 2vw;
}

.hh3.col.white {
  color: white;
}

.hh4 {
  font-size: 3vh;
  margin-bottom: 5vh;
  color:#26428b;
  font-weight: bold;
  margin-bottom: 2vh;
}

.hh4.rect {
  text-align: left;
  padding-left: 2vw;
  font-size: 4vh;
  font-weight: normal;
  line-height: 1.3em;
}

.column {
  width: 33.33%; 
  padding: 1EM;   
  padding-bottom: 0;
  display: table-cell;
}

.row {
  width: 75%;
  margin-left: 9%;
  margin-right: 9%;
  display: table;
}
  
  /* Clear floats after the columns */
.row:after {
  content: "";
  display: table;
  clear: both;
}

.rectangle {
  margin-bottom: 5vh;
  height: 28vh;
  width: 70vw; /*why doesn't setting width to 100% work?*/
  align-items: center;
  background-color: #e6f2f6;
}

img {
  object-fit: contain; /*do i want contain or cover? do i want img width to b consistent*/
}

img.left {
  height: 29vh;
  margin-right: 2vw;
  float: left;
}

img.right {
  height: 29vh;
  float: right;
  margin-left: 2vw;
}

img.final {
  width: 79vw;
}

.vl {
  width: 1vh;
  height: 14.25vh;
  margin-left: 2.5vw;
  margin-right: 1vw;
  background-color:#26428b;
}

.vl.collab {
  margin-left: .9vw;
}

/* Container needed to position the button. Adjust the width as needed */
.container {
  position: relative;
  align-items: center;
}

/* Make the image responsive */
.container img {
  align-items: center;
  width: 79vw;
  height: auto;
}

/* Style the button and place it in the middle of the container/image */
.container .btn {
  position: absolute;
  top: 35%;
  left: 0%;
  background-color: white;
  color: #26428b;
  font-size: 7vh;
  font-weight: bold;
  padding: 12px 24px;
  border: none;
  cursor: pointer;
  border-radius: 5px;
  align-items: center;
}

.container .btn:hover {
  background-color: #e6f2f6;
}

@media screen and (max-width: 560px) {
  .column {
    display: block;
    width: 100%;
    height: 40vmax;
    padding: .5EM;
  }
  .hh2.col {
    font-size: 6vmax;
    margin-left: 2vw;
    margin-right: 2vw;
    text-align: center;
  }
  .hh3 {
    font-size: 6vmin;
    margin-bottom: 10%;
    margin-top: 1.5vh;
  }
  .container img {
    width: 79vw;
    height: auto;
  }
  .container .btn {
    position: absolute;
    top: 9%;
    left: 8%;
    background-color: white;
    color: #26428b;
    font-size: 6vmin;
    font-weight: bold;
    padding: 5px 15px;
    border: none;
    cursor: pointer;
    border-radius: 5px;
  }
  img.left {
    float: right;
  }
}

@media screen and (max-width: 900px) {
  .rectangle {
    height: auto;
    width: 72vw;
  }
  img.left {
    height: auto;
  }
  img.right {
    height: auto;
  }
}
